.b-warning {
  padding-top: vie(35, xl);
  padding-bottom: vie(19, xl);

  &__text {
    width: 100%;
    font-family: $arial;
    color: #e0e0e0;
    font-size: vie(59, xl);
    text-transform: uppercase;
    font-weight: 300;
    line-height: vie(64, xl);
  }

  &__descr {
    font-size: vie(15, xl);
    color:  #727272;
  }
}

.privacy-policy {
  text-decoration: underline;
}